@import "./src/styles/variables";

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: 0.01em;
  padding-top: 30px;
  position: relative;
  @media (max-width: 760px) {
    flex-direction: column;
    align-items: center;
  }

  &::before {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    top: 0;
    background-color: $color-grey;
  }

  .title-width {
    display: flex;
    align-items: center;
    width: 400px;
    @media (max-width: 760px) {
      width: auto;
    }

    .cart-item__img {
      width: 80px;
      height: 80px;
      margin-right: 10px;
    }

    .cart-item__title {
      span {
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.01em;
        color: #8D8D8D;
      }
    }
  }

  .buttons-width {
    display: flex;
    align-items: center;
    width: 300px;
    justify-content: space-between;
    margin: 10px;

    .cart-item__amount {
      span {
        border: 1px solid $color-primary;
        color: $color-primary;
        height: 30px;
        width: 30px;
        display: inline-block;
        border-radius: 50%;
        text-align: center;
        cursor: pointer;
        vertical-align: middle;
      }

      .amount {
        border: none;
        color: inherit;
        margin: 0 10px;
        cursor: auto;
        display: inline-block;
        width: 12px;
      }
    }

    .cart-item__remove {
      span {
        border: 1px solid #8D8D8D;
        color: #8D8D8D;
        height: 30px;
        width: 30px;
        display: inline-block;
        border-radius: 50%;
        text-align: center;
        cursor: pointer;
        vertical-align: middle;
        padding-top: 1px;
      }
    }
  }
}